import React from 'react';

import { useSelector } from 'react-redux';

import { getEmail, recovery } from '../../data';

import { alert } from '../../util';
import FeatherIcon from 'feather-icons-react';
import '../Auth/index.css';

const Component = ({ history }) => {
    const ui = useSelector(state => state.ui);

    const _handleForm = e => {
        e.preventDefault();
        if (_handleCheckEmail) {
            const form = document.recovery;
            recovery(form.email.value, res => {
                if (res === 200) {
                    alert('message', ui.lang === 'en_US' ? 'Password recovery mail has sent :)' : '   .', () => history.goBack(), null);
                } else {
                    alert('message', ui.lang === 'en_US' ? 'Email is invalid :(' : '  .', null, null);
                }
            });
        } else {
            alert('messeage', 'Input data is invalid! Please check your information again.', null, null);
        }
    };

    const _handleBack = () => {
        history.goBack();
    };

    const normalColor = '#505050';
    const confirmedColor = '#64FFDA';

    const _handleCheckEmail = () => {
        const value = document.recovery.email.value;
        const target = 'recovery-email';

        if (value.length < 5) {
            document.getElementById(target).style.color = normalColor;
            return false;
        }

        getEmail(value, res => {
            if (res.status === 200) {
                document.getElementById(target).style.color = confirmedColor;
                return true;
            } else {
                document.getElementById(target).style.color = normalColor;
                return false;
            }
        });
    };

    return (
        <div className='auth'>
            <div className='auth-container'>
                <div className='auth-title'>{ui.lang === 'en_US' ? 'Password Recovery' : ' '}</div>
                <div className='auth-description'>
                    {ui.lang === 'en_US'
                        ? 'Sometimes, all you need to do is completely make an ass of yourself and laugh it off to realise that life isnt so bad after all. The green tea and avocado smoothie turned out exactly as would be expected.'
                        : '       ,      .   1988 2 25 . ,                         .'}
                </div>
                <form name='recovery' autoComplete='off' onSubmit={_handleForm}>
                    <div className='auth-placeholder'>
                        {ui.lang === 'en_US' ? 'EMAIL' : ''}
                        <div className='auth-input-check'>
                            <FeatherIcon id='recovery-email' className='auth-input-check-icon' icon='check' />
                        </div>
                    </div>
                    <input className='auth-input' type='email' name='email' autoComplete='password' autoFocus onChange={_handleCheckEmail} />
                    <input className='auth-button auth-submit' type='submit' value={ui.lang === 'en_US' ? 'SUBMIT' : ' '} />
                    <input className='auth-button' type='button' value={ui.lang === 'en_US' ? 'BACK' : ''} onClick={_handleBack} />
                </form>
            </div>
        </div>
    );
};

export default Component;
