import React from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { auth_token } from '../../actions';

import { Link } from 'react-router-dom';
import cookie from 'react-cookies';

import SocialSignIn from '../SocialSignIn';

import { signin } from '../../data';

import { alert } from '../../util';
import '../Auth/index.css';

const Component = ({ history }) => {
    const ui = useSelector(state => state.ui);
    const dispatch = useDispatch();

    const _handleBack = () => {
        history.goBack();
    };

    const _handleForm = e => {
        e.preventDefault();
        const form = document.signin;
        signin(form.userName.value, form.password.value, res => {
            if (res.status === 200) {
                dispatch(auth_token(res.obj));
                alert(
                    'question',
                    ui.lang === 'en_US' ? 'Do you want to keep your signin status?' : ' ?',
                    cookie.save('token', res.obj, {
                        path: '/'
                    }),
                    cookie.save('token', res.obj, {
                        path: '/',
                        maxAge: 60 * 30
                    })
                );
                history.goBack();
            } else {
                if (res.status === 411) {
                    alert('message', ui.lang === 'en_US' ? 'Username is invalid :(' : '  .', null, null);
                } else if (res.status === 412) {
                    alert('message', ui.lang === 'en_US' ? 'Password is invalid :(' : '  .', null, null);
                } else {
                    alert('message', ui.lang === 'en_US' ? 'Something went wrong :(' : '     :(', null, null);
                }
            }
        });
    };

    return (
        <div className='auth'>
            <div className='auth-container'>
                <div className='auth-title'>{ui.lang === 'en_US' ? 'Sign In' : ''}</div>
                <div className='auth-description'>
                    {ui.lang === 'en_US'
                        ? 'Sometimes, all you need to do is completely make an ass of yourself and laugh it off to realise that life isnt so bad after all. The green tea and avocado smoothie turned out exactly as would be expected.'
                        : '       ,      .   1988 2 25 . ,                         .'}
                </div>
                <form name='signin' autoComplete='off' onSubmit={_handleForm}>
                    <div className='auth-placeholder'>{ui.lang === 'en_US' ? 'USER NAME' : ''}</div>
                    <input className='auth-input' type='text' name='userName' autoFocus />
                    <div className='auth-placeholder'>{ui.lang === 'en_US' ? 'PASSWORD' : ''}</div>
                    <input className='auth-input' type='password' name='password' />
                    <Link to='/auth/signup'>
                        <div className='auth-nav'>{ui.lang === 'en_US' ? "Don't you have an account yet? Just Sign Up for free!" : '  ?   !'}</div>
                    </Link>
                    <Link to='/auth/recovery'>
                        <div className='auth-nav'>{ui.lang === 'en_US' ? 'Do you Forgot Password?' : ' ?'}</div>
                    </Link>
                    <input className='auth-button auth-submit' type='submit' value={ui.lang === 'en_US' ? 'SIGN IN' : ''} />
                    <input className='auth-button' type='button' value={ui.lang === 'en_US' ? 'BACK' : ''} onClick={_handleBack} />
                </form>
                <div className='auth-division'>
                    <div className='auth-division-line'></div>
                    <div className='auth-division-text'>{ui.lang === 'en_US' ? 'OR' : ''}</div>
                </div>
                <div className='auth-title'>{ui.lang === 'en_US' ? 'Sign In with Social Media' : '  '}</div>
                <div className='auth-description'>
                    {ui.lang === 'en_US'
                        ? 'Sometimes, all you need to do is completely make an ass of yourself and laugh it off to realise that life isnt so bad after all.'
                        : '       ,      .   1988 2 25 .'}
                </div>
                <SocialSignIn />
            </div>
        </div>
    );
};

export default Component;
